<template>
  <div class="centered-container">
    <md-content class="md-elevation-3">

      <div class="title">
        
        <div class="md-title">Hooked || CRM</div>
        <div class="md-body-1">Set it and Forget it</div>
      </div>

      <div class="form">
        <md-field>
          <label>E-mail</label>
          <md-input v-model="login.email" autofocus></md-input>
        </md-field>

        <md-field md-has-password>
          <label>Password</label>
          <md-input v-model="login.password" type="password"></md-input>
        </md-field>
      </div>

      <div class="actions md-layout md-alignment-center-space-between">
        <a href="/resetpassword">Reset password</a>
        <md-button class="md-raised md-primary" @click="authenticate">Log in</md-button>
      </div>

      <div class="loading-overlay" v-if="loading">
        <md-progress-spinner md-mode="indeterminate" :md-stroke="2"></md-progress-spinner>
      </div>

    </md-content>
    <div class="background" />
  </div>
</template>

<script>
import { signInWithEmailAndPassword } from 'firebase/auth';
import { auth } from '@/components/Cards/firebaseConfig.js';

export default {
  data() {
    return {
      login: {
        email: '',
        password: '',
      },
      loading: false,
    };
  },
  methods: {
    async authenticate() {
      try {
        this.loading = true;

        // Sign in with Firebase Authentication
        await signInWithEmailAndPassword(
          auth,
          this.login.email,
          this.login.password
        );

        // Handle successful login (e.g., navigate to the dashboard)
        this.$router.push('/dashboard');
      } catch (error) {
        
        // Handle login error (e.g., display error message)
      } finally {
        this.loading = false;
      }
    },
  },
};
</script>

<style lang="scss">
.centered-container {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 100vh;
  .title {
    text-align: center;
    margin-bottom: 30px;
    img {
      margin-bottom: 16px;
      max-width: 80px;
    }
  }
  .actions {
    .md-button {
      margin: 0;
    }
  }
  .form {
    margin-bottom: 60px;
  }
  .background {
    background: url(data:image/svg+xml;base64,);
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 0;
  }
  .md-content {
    z-index: 1;
    padding: 40px;
    width: 100%;
    max-width: 400px;
    position: relative;
  }
  .loading-overlay {
    z-index: 10;
    top: 0;
    left: 0;
    right: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.9);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  /*change the login button background color to blue*/
  .md-primary.md-button.md-raised {
    background-color: #2c2c2c !important;
  }
}
</style>